<template>
	<div id="background">
		<div id="result-box">
			<h1>在售手机列表</h1>

			<Card v-for="group of groups" class="card">
        <p slot="title">{{group.title}}</p>
        <Button type="primary" shape="circle" size="small" slot="extra" @click="enterGroup(group.title)">加入购物车</Button>
        <!-- <Button type="error" shape="circle" size="small" slot="extra">退出小组</Button> -->
        <p>{{group.messges}}</p>
      </Card>
		</div>
	</div>
</template>
<script>
import router from '../../router'
import { getJoinedGroups } from '../../api/api'
export default {
	mounted: function() {
		this.getGroups()
	},
	methods: {
		getGroups: function() {
    let options = {
      username: window.localStorage.getItem('username'),
      pageNum: 1,
      pageSize: 10,
    }
    getJoinedGroups(options)
      .then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            console.log(res.data.data.list)
            this.groups = res.data.data.list
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info(`未知错误, 无法获取数据，状态码:${res.status}`)
        }
      })
  	},
  	enterGroup: function(title) {
  		router.push(`/home/groups/${title}`)
  	}
	},
	data: function() {
		return {
			groups: [
				
			]
		}
	}
}
</script>
<style scoped>
#result-box {
	width: 900px;
  margin: 0 auto;
}
</style>


