<template>
  <div id="background">
    <div id="search-box">
      <strong>小组搜索</strong>
      <div>
        <Input v-model="value" icon="search" @on-enter="searchGroupsByTitle"></Input>
      </div>
    </div>
 <!--    <div id="progress-box" v-id="isSearching">
      <Progress :percent="45" status="active" :stroke-width="5" hide-info></Progress>
    </div> -->
    <div id="results-box">
      <Card v-for="group of groups" class="card">
        <p slot="title">{{group.title}}</p>
        <Button type="primary" shape="circle" size="small" slot="extra" @click="joinAGroup(group.title)">加入小组</Button>
        <p>{{group.messges}}</p>
      </Card>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { searchGroups, joinGroup } from '../../api/api'
export default {
  data: function() {
    return {
      isSearching: true,
      groups: [],
      totalPage: 0,
      value: '',
    }
  },
  methods: {
    searchGroupsByTitle: function() {
      let options = {
        titleCondition: this.value,
        pageNum: 1,
        pageSize: 10,
      }
      searchGroups(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              console.log(res.data.data.list)
              this.groups = res.data.data.list
              this.totalPage = res.data.total
            } else {
              this.$Message.info(res.data.msg)
            }
          } else {
            this.$Message.info(`未知错误,状态码:${res.status}`)
          }
        })
    },
    joinAGroup: function(title) {
      let options = {
        title: title
      }
      joinGroup(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              this.$Message.info(res.data.msg)
              router.push('/home/groups')
            } else {
              this.$Message.info(res.data.msg)
            }
          } else {
            this.$Message.info(`未知错误,状态码:${res.status}`)
          }
        })
    },
  }
}
</script>
<style scoped>
div {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
#search-box {
  width: 400px;
  margin: 0 auto;
  margin-top: 30px;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
  background: -webkit-linear-gradient(left top, #66f9cf, #2d8cf0);
}
#search-box div {
  margin: 0 auto;
  width: 300px;
  margin-bottom: 10px;
}
#search-box strong {
  color: #FFFFFF;
}
#progress-box {
  width: 400px;
  margin: 0 auto;
}
#page {
  margin-top: 10px;
}
#results-box {
  width: 900px;
  margin: 0 auto;
}
.card {
  margin-top: 5px;
}
#page {
  margin-top: 10px;
}
</style>


